import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from "../views/Dashboard";
import Account from "../views/Account";

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/account',
      name: 'account',
      component: Account
    }
  ]
})



