<template>
  <div style="margin-bottom: 10em">
    <nav class="blue-grey">
      <div class="nav-wrapper">
        <router-link to="/" class="brand-logo ">Comaction</router-link>
        <ul id="nav-mobile" class="right hide-on-med-and-down">
          <li v-if="userRole==3"><a href="" class="">Partenaires</a></li>
          <li v-if="userRole==2"><a href="" class="">Agences</a></li>
          <li v-if="userRole==2"><a href="" class="">Commerciaux</a></li>
          <li v-if="userRole==1"><a href="" class="">Souscription</a></li>
          <li><router-link to="/contact" class="">Contact</router-link></li>
          <li><a href="/account" class="">Mon compte</a></li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "navbar",
      data(){
          return{
            isConnected: false,
            userRole: 0
          }
      },
      methods: {
          getUserData: function(jwt){
            axios.post('api/user/validate_token.php', {
              "jwt": jwt
            })
              .then(function (response) {
                localStorage.setItem('userData', JSON.stringify(response.data.data))
              })
              .catch(function (error) {
                console.log(error);
              })
          }
      },
      created() {
        this.isConnected = localStorage.getItem('jwt') !== ''
        if(this.isConnected) {
          this.getUserData(localStorage.getItem('jwt'))
          this.userData = JSON.parse(localStorage.getItem('userData'))
        }else{
          localStorage.setItem('jwt', '');
          localStorage.setItem('userData', '{}');
        }
      }
    }
</script>



