<template>
  <div>
    <NavBar></NavBar>
    <div class="row">
      <div class="col s12 m6 offset-m3">
        <div class="card blue-grey lighten-3">
          <div class="card-content row" v-if="!isConnected">
            <div class="col s10" id="login" >
              <h2>Connexion</h2>
              <form id='login_form' @submit.prevent="login">
                <div id="response"></div>
                <div class='form-group'>
                  <label for='email'>Adresse mail</label>
                  <input type='email' class='form-control' ref="email" id='email' name='email'
                         placeholder='Enter email'>
                </div>
                <div class='form-group'>
                  <label for='password'>Mot de passe</label>
                  <input type='password' class='form-control' ref="password" id='password' name='password'
                         placeholder='Password'>
                </div>
                <button type='submit' class='btn btn-primary'>Login</button>
              </form>
            </div>
          </div>
          <div class="card-content row" v-if="isConnected">
            <div class="col s10" id="userDetails" >
              <h4 class="roboto">Connecté en tant que {{userData.firstname}} {{userData.lastname}}</h4>
            </div>
            <div class="col s12 center-align" id="logout">
              <button class='btn btn-primary blue-grey' v-on:click="logout">Déconnexion</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import NavBar from "../components/NavBar";
  import axios from "axios";

  export default {
    name: 'account',
    data() {
      return {
        isConnected: false,
        userData: []
      }
    },
    methods: {
      login: function () {
        localStorage.setItem('jwt', '');
        localStorage.setItem('userData', '{}');
        var self = this
        axios.post('api/user/login.php', {
          "email": this.$refs.email.value,
          "password": this.$refs.password.value
        })
          .then(function (response) {
            localStorage.setItem('jwt', response.data.jwt)
            self.getUserData(response.data.jwt)
            window.location = "/"
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      logout: function () {
        localStorage.setItem('jwt', '');
        localStorage.setItem('userData', '{}');
        window.location = "/"
      },
      getUserData: function(jwt){
        axios.post('api/user/validate_token.php', {
          "jwt": jwt
        })
          .then(function (response) {
            localStorage.setItem('userData', JSON.stringify(response.data.data))
          })
          .catch(function (error) {
            console.log(error);
          })
      },
    },
    created() {
      this.isConnected = localStorage.getItem('jwt') !== ''
      if(this.isConnected) {
        this.getUserData(localStorage.getItem('jwt'))
        this.userData = JSON.parse(localStorage.getItem('userData'))
      }else{
        localStorage.setItem('jwt', '');
        localStorage.setItem('userData', '{}');
      }
    },
    components: {NavBar},
  }
</script>



